  .l-header__trigger {
    position: absolute;
    z-index: 100;
    top: 0;
    right: 0;
    visibility: hidden;
    opacity: 0; }
    @media not all and (min-width: 1060px) {
      .l-header__trigger {
        visibility: inherit;
        opacity: 1; } }
    @media screen and (max-width: 767px) {
      .l-header__trigger {
        visibility: inherit;
        opacity: 1; } }
    .l-header__trigger.is-active {
      visibility: inherit;
      opacity: 1; }
    .l-header__triggerBtn {
      display: table-cell;
      position: relative;
      width: 80px;
      height: 80px;
      margin-right: 20px;
      margin-top: 11px;
      margin-bottom: 9px;
      padding-top: 24px;
      padding-right: 25px;
      padding-bottom: 35px;
      padding-left: 25px;
      border-color: transparent;
      border-radius: 40px;
