          height: 60px;
          margin-top: 6px;
          margin-right: 0;
          margin-bottom: 0;
          padding: 0;
          background-color: transparent; } }
    .l-header__triggerLine {
      display: block;
      position: absolute;
      top: 30px;
      left: 50%;
      width: 30px;
      height: 2px;
      background-color: #fff;
      transform: translateX(-50%);
      transition: 0.4s; }
      @media screen and (max-width: 767px) {
        .l-header__triggerLine {
          top: 25px;
          width: 20px;
          background-color: #195cb8; } }
      .l-header__triggerLine.is-active {
        background-color: transparent; }
      .l-header__triggerLine::before {
        content: "";
        display: block;
        position: absolute;
        top: -6px;
        width: 100%;
        height: 100%;
        background-color: #fff;
        transition: inherit; }
        @media screen and (max-width: 767px) {
          .l-header__triggerLine::before {
            background-color: #195cb8; } }
      .l-header__triggerLine.is-active::before {
        top: 0;
        transform: rotate(45deg); }
        @media screen and (max-width: 767px) {
          .l-header__triggerLine.is-active::before {
            background-color: #fff; } }
      .l-header__triggerLine::after {
        content: "";
        display: block;
        position: absolute;
        top: 6px;
        width: 100%;
        height: 100%;
        background-color: #fff;
        transition: inherit; }
        @media screen and (max-width: 767px) {
          .l-header__triggerLine::after {
            background-color: #195cb8; } }
      .l-header__triggerLine.is-active::after {
        top: 0;
        transform: rotate(-45deg); }
        @media screen and (max-width: 767px) {
          .l-header__triggerLine.is-active::after {
            background-color: #fff; } }
    .l-header__triggerText {
      position: absolute;
      bottom: 15px;
      left: 50%;
      width: 100%;
      transform: translateX(-50%); }
      @media screen and (max-width: 767px) {
        .l-header__triggerText {
          bottom: 5px; } }
      .l-header__triggerText::before {
        content: "MENU";
        font-family: "Yu Gothic Medium", "游ゴシック", "Yu Gothic", "游ゴシック", YuGothic, "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo";
        font-size: 1rem;
        font-weight: 700;
        color: #fff;
        letter-spacing: 0.02em;
        text-align: center; }
        @media screen and (max-width: 767px) {
          .l-header__triggerText::before {
            color: #195cb8; } }
      .l-header__triggerText.is-active::before {
        content: "CLOSE"; }
        @media screen and (max-width: 767px) {
          .l-header__triggerText.is-active::before {
            color: #fff; } }

